.trip {
    margin: 4rem 6rem;
    color: #2a2a2a;
}
.trip h1 {
    font-size: 3rem;
}

.tripCard {
    margin-top: 3rem;
    display: flex;
    justify-content: space-between;
}
.tCard {
    width: 32%;
    text-align: start;
    box-shadow: 0 5px 25px 2px rgba(0,0,0,0.11);
    border-radius: 7px;
    cursor: pointer;
    padding: 1rem .8rem;
}
.tImage {
    height: 250px;
    overflow: hidden;
    border-radius: 7px;
}
.tImage img {
    width: 100%;
    height: 100%;
    transition: 0.3s ease-in-out;
    border-radius: 7px;
    object-fit: cover;
}
.tImage:hover img {
    transform: scale(1.3);
}
.tCard h4 {
    font-size: 1.3rem;
    padding: .9rem 1rem 0;
}
.tCard p {
    padding: .5rem 1rem;
}

@media screen and (max-width:850px) {
    .trip {
        margin: 4rem 2rem;
    }
    .tripCard {
        flex-direction: column;
    }
    .tCard {
        width: 100%;
        margin-bottom: 1.5rem;
    }
}